<template>
     <div>
       <div v-if="activar_alert!=''" class="alert alert-warning" role="alert">
         {{activar_alert}}
       </div>
<div class="container">
                <div class="timetable-img text-center">
                    
                </div>
                <div class="table-responsive">
                    <table class="table table-bordered text-center">
                        <thead>
                            <tr class="bg-light-gray">
                                <th class="text-uppercase">{{columnas_et}}
                                </th>
                                <th class="text-uppercase"
                                v-for="columna in columnas" v-bind:key="columna.iden">{{columna.label}}</th>
                            </tr>
                        </thead>
                        <tbody>
                              <tr v-for="fila in filas" v-bind:key="fila.iden">
                                <td class="align-middle">{{fila.label}}</td>
                                   <evento v-for="columna in columnas" v-bind:key="columna.iden+'_'+fila.iden" :registro="ob_asignacion(fila.iden,columna.iden)"
                                   :fila="fila.iden"
                                   :columna="columna.iden"
                                   @captura-observacion="mostrar_observacion"
                                   @click_celda="registrar_asignacion"></evento>
                              </tr>                            
                        </tbody>
                    </table>
                </div>
            </div>



  </div>
</template>

<script>
import Event from './Event.vue';
export default {
  name: 'app',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
      ,columnas_et:'Estaciones'
      ,mensaje_hijo:'Hola mundo'
      ,activar_alert:''
      
     
    }
  }
  ,props:['filas','columnas','asignaciones']
  ,components:{
    'evento':Event
  }
  ,methods:
  {
    ob_asignacion:function(fila,columna){
      var resultado=this.asignaciones.filter(function(item){
        if((item.fila==fila)&&(item.columna==columna))
        return true;
        else
        return false;
      });
      if(resultado.length==0)
      return null;
      else
      return resultado[0];
      //console.log(resultado);
    }
    ,registrar_asignacion:function(fila,columna){
         this.$emit('celda_activa',fila,columna);
    }
    ,mostrar_observacion:function(mensaje){
           this.activar_alert=mensaje;
    }
  }
}

</script>

<style lang="scss">
body{
    margin-top:20px;
}

.padding-15px-lr {
    padding-left: 15px;
    padding-right: 15px;
}
.padding-5px-tb {
    padding-top: 5px;
    padding-bottom: 5px;
}
.margin-10px-bottom {
    margin-bottom: 10px;
}
.border-radius-5 {
    border-radius: 5px;
}

.margin-10px-top {
    margin-top: 10px;
}
.font-size14 {
    font-size: 14px;
}

.text-light-gray {
    color: #d6d5d5;
}
.font-size13 {
    font-size: 13px;
}

.table-bordered td, .table-bordered th {
    border: 1px solid #dee2e6;
}
.table td, .table th {
    padding: .75rem;
    vertical-align: top;
    border-top: 1px solid #dee2e6;
}
</style>


