<template>
  <td :class="formato_celda(registro)"
 @click="activar_celda">
  <div v-if="registro">
     <span class="padding-5px-tb padding-15px-lr border-radius-5    margin-10px-bottom text-white font-size16 xs-font-size13" :class="formato(registro.tipos)">
        {{registro.tipos}}
     </span>
        <div class="margin-10px-top font-size14">
            {{registro.cliente}}
            <input type="text" class="form-control" @input="avisar_papa($event)">
        </div>
        
  </div>
  </td>
</template>

<script>
export default{
    data(){
        return{
            dato1:'Estees un dato interno'
        }
    }
    ,props:['registro',"fila","columna"]
    ,methods:{
        formato:function(tipo){
      switch(tipo){
        case 'Normal':
        return{
            'bg-sky':true
        }
        break;
        case 'VIP':
        return{
            'bg-purple':true
        }
        break;
        case 'Premium':
        return{
            'bg-green':true
        }
        break;
        case 'Medio':
        return{
            'bg-orange':true
        }
        break;

      }
    }
    ,formato_celda:function(asignacion){
     if(!asignacion)
     return {'bg-light-gray':true}; 
    }
    ,activar_celda:function (){
        if(!this.registro)
         this.$emit("click_celda",this.fila,this.columna);
    }
    ,avisar_papa:function($event){
        this.$emit("captura-observacion",$event.target.value);
    }
    }
}
</script>

<style>
.bg-light-gray {
    background-color: #f7f7f7;
}
.table-bordered thead td, .table-bordered thead th {
    border-bottom-width: 2px;
}
.table thead th {
    vertical-align: bottom;
    border-bottom: 2px solid #dee2e6;
}
.table-bordered td, .table-bordered th {
    border: 1px solid #dee2e6;
}


.bg-sky.box-shadow {
    box-shadow: 0px 5px 0px 0px #00a2a7
}

.bg-orange.box-shadow {
    box-shadow: 0px 5px 0px 0px #af4305
}

.bg-green.box-shadow {
    box-shadow: 0px 5px 0px 0px #4ca520
}

.bg-yellow.box-shadow {
    box-shadow: 0px 5px 0px 0px #dcbf02
}

.bg-pink.box-shadow {
    box-shadow: 0px 5px 0px 0px #e82d8b
}

.bg-purple.box-shadow {
    box-shadow: 0px 5px 0px 0px #8343e8
}

.bg-lightred.box-shadow {
    box-shadow: 0px 5px 0px 0px #d84213
}


.bg-sky {
    background-color: #02c2c7
}

.bg-orange {
    background-color: #e95601
}

.bg-green {
    background-color: #5bbd2a
}

.bg-yellow {
    background-color: #f0d001
}

.bg-pink {
    background-color: #ff48a4
}

.bg-purple {
    background-color: #9d60ff
}

.bg-lightred {
    background-color: #ff5722
}
</style>



