<template>
<div>
  <datepicker :language='es'
  input-class="form-control"
  v-model="fecha"
  @input="ver_dia"
  format="yyyy-MM-dd"></datepicker>
  <timetable :filas="filas"
             :columnas="columnas"
             @celda_activa="mostrar_modal"
             :asignaciones="asignaciones"></timetable>



             <div id="modal" class="modal fade" tabindex="-1" role="dialog">
  <div class="modal-dialog" role="document">
    <div class="modal-content">
      <div class="modal-header">
        <button type="button" class="close" data-dismiss="modal" aria-label="Close"><span aria-hidden="true"></span></button>
        <h4 class="modal-title">Modal title</h4>
      </div>
      <div class="modal-body">
        <form action="">

          <div class="form-group">
            <label for="">Cliente</label>
            <select name="" id="" class="form-control" v-model="nueva_asignacion.socio" >
             <option v-for="socio in socios" 
            v-bind:key="socio.idsocio"  :value="socio.idsocio">
            {{socio.nombre}}</option>
            </select>
          </div>

          <div class="form-group">
            <label for="">Personal</label>
            <input type="text" class="form-control"
            v-model="nueva_asignacion.personal">
          </div>

          <div class="form-group">
            <label for="">Placa</label>
            <input type="text" class="form-control"
            v-model="nueva_asignacion.placa">
          </div>
          
          <div class="form-group">
            <label for="">Modelos</label>
            <input type="text" class="form-control"
            v-model="nueva_asignacion.modelo">
          </div>
          
          <div class="form-group">
            <label for="">Ao</label>
            <input type="text" class="form-control"
            v-model="nueva_asignacion.anio">
          </div>

          <div class="form-group">
            <label for="">Tipos</label>
            <select name="" id="" class="form-control"
            v-model="nueva_asignacion.tiposer">
              <option v-for="tipose in tiposer" v-bind:key="tipose.idtiposervicio" 
              :value="tipose.idtiposervicio">{{tipose.nombre_servicio}}</option>
            </select>
          </div>
        </form>
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-default" data-dismiss="modal">Close</button>
        <button type="button" class="btn btn-primary"
        @click="insertar_asignacion">Registrar</button>
      </div>
    </div><!-- /.modal-content -->
  </div><!-- /.modal-dialog -->
</div><!-- /.modal -->
</div>
</template>

<script>
import TimeTable from './TimeTable.vue';
import Datepicker from 'vuejs-datepicker';
import {es} from 'vuejs-datepicker/dist/locale';
import axios from 'axios';
export default{
  data(){
    return{
       columnas:columnas_iniciales
      ,filas:filas_iniciales
      ,asignaciones:asignaciones_iniciales
      ,es:es
      ,fecha:''
      ,token:tk
      ,socios:lista_socios
      ,tiposer:lista_tipos
      ,url:url_envio
      ,url_insertar:url_insert
      ,sincrono:bandera_sincrona
      ,nueva_asignacion:{
        fila:1
        ,columna:1
        ,socio:2
        ,personal:''
        ,placa:''
        ,modelo:''
        ,anio:''
        ,tiposer:1
        ,fecha:""
      }
      
    }
  }
  ,methods:{
    formato_fecha:function(fecha){
        var mes=fecha.getMonth()+1;
        var label_mes=mes.toString();
        if(mes<10)
        label_mes='0'+label_mes;

        var dia=fecha.getDate();
        var label_dia=dia.toString();
        if(dia<10)
        label_dia='0'+label_dia;

        return fecha.getFullYear()+'-'+label_mes+'-'+label_dia;
    }
    ,ver_dia:function(fecha){
      this.nueva_asignacion.fecha=this.formato_fecha(fecha);
      if(this.sincrono){

        document.querySelector('#oculto').value=this.formato_fecha(fecha);
        document.frm.submit();
      }
      else{
var datos = {
        fecha:this.formato_fecha(fecha)
        ,_token:this.token
      };
      axios.post(this.url,datos).then((result) =>{
        for( var i=0;i<result.data.length;i++){
          this.asignaciones.push(result.data[i]);
        }
 
      });
    }
      }
      ,mostrar_modal:function(fila,columna){
            this.nueva_asignacion.fila=fila;
            this.nueva_asignacion.columna=columna;
            this.nueva_asignacion.cliente='';
            this.nueva_asignacion.tipos='Normal';
        $('#modal'). modal('show'); 
      }
      ,insertar_asignacion:function(){
        var datos = {
        servicio:this.nueva_asignacion
        ,_token:this.token
      };

      axios.post(this.url_insertar,datos).then((result) =>{
         if(result.data.status=='Error'){
           alert(result.data.mensaje);
         }
         else{
           alert('Servicio Guardado Correctamente');
        this.asignaciones.push({
        fila:this.nueva_asignacion.fila
        ,columna:this.nueva_asignacion.columna
        ,cliente:result.data.cliente
        ,tipos:result.data.tipos
      });
      $('#modal'). modal('hide');
         }
      })
      .catch(function(error){
        alert('Error del servidor');
        console.log(error)
        });
        console.log(this.nueva_asignacion);
      
    }

     
  }
  ,components:{
    'timetable':TimeTable
    ,'datepicker':Datepicker
  }
}
</script>

<style lang="scss">
</style>



